import React, { useEffect, useState, Fragment } from "react";
import { makeStyles } from "@material-ui/core/styles";
import MenuOpen from "@material-ui/icons/MenuOpenRounded";
import Home from "@material-ui/icons/HomeRounded";
import MenuBook from "@material-ui/icons/MenuBookRounded";
import Book from "@material-ui/icons/BookRounded";
import { Link, Route, Switch } from "react-router-dom";
import clsx from "clsx";
import ThumbUp from "@material-ui/icons/ThumbUpRounded";
import ContactMail from "@material-ui/icons/ContactMailRounded";
import Info from "@material-ui/icons/InfoRounded";
import "./../assets/style.scss";
import HomePage from "./HomePage";
import Blog from "./Blog";
import ContactUs from "./ContactUs";
import Donate from "./Donate";
import SendCourse from "./SendCourse";
import LearningPage from "./LearningPage";
import About from "./About";
import {
  BottomNavigation,
  BottomNavigationAction,
  Drawer,
  List,
  Divider,
  ListItem,
  ListItemText,
  ListItemIcon,
} from "@material-ui/core";

const drawerWidth = 200;

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    position: "fixed",
    bottom: 0,
  },
  appBar: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginRight: drawerWidth,
    backgroundColor: "white",
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
    background:
      "linear-gradient(0deg, rgba(13,71,161 ,1) 50%, rgba(30,136,229 ,1) 100%)",
  },
  // necessary for content to be below app bar
  toolbar: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
  },
  textRight: {
    textAlign: "right",
    direction: "rtl",
    color: "white",
  },
  Vazir: {
    fontFamily: "Vazir",
  },
  active: {
    background: "rgba(0,0,0 ,0.2)",
    transition: ".8s",
    "&:hover": {
      background: "rgba(0,0,0 ,0.2)",
      transition: ".8s",
    },
  },
  icons: {
    color: "rgba(69,90,100 ,1)",
    fontSize: "33px",
    transition: ".6s",
  },
  toolbarCenter: {
    textAlign: "left",
    display: "inline-block",
  },
  iconLink: {
    margin: "14px 18px",
    display: "inline-block",
  },
  teleHover: {
      color: "rgba(21,101,192 ,1)",
      transition: ".6s",
  },
  twHover: {
      color: "rgba(33,150,243 ,1)",
      transition: ".6s",
  },
  instaHover: {
      color: "rgba(255,23,68 ,1)",
      transition: ".6s",
  },
  whHover: {
      color: "rgba(0,150,136 ,1)",
      transition: ".6s",
  },
  colorBlack: {
    color: "black",
    textAlign: "right",
    direction: "rtl",
    float: "right",
    marginTop: "14px",
    fontSize: "25px",
  },
  colorWhite: {
    color: "#FAFAFA",
  },
  navigationShadow: {
    boxShadow: "0px 2px 4px gray",
    zIndex : "10"
  },
  linkColor:{
    color: "#1976D2"
  }
}));

const App = () => {
  const classes = useStyles();
  const [value, setValue] = React.useState("home");
  const [active, setActive] = useState(window.location.hash);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const [state, setState] = React.useState({
    right: false,
  });

  let div = "<DIV>MR</DIV>";

  useEffect(() => {
    window.onpopstate = () => {
      setActive(window.location.hash);
      let location = window.location.hash;
      if (location.substr(1) === "/learning") {
        setValue("learning");
      } else if (location.substr(1) === "/blog") {
        setValue("blog");
      } else if (location.substr(1) === "") {
        setValue("home");
      } else if (location.substr(1) === "contact-us") {
        setValue("other");
      } else if (location.substr(1) === "donate") {
        setValue("other");
      } else if (location.substr(1) === "about") {
        setValue("other");
      }
    };
    let location = window.location.hash;
    if (location.substr(1) === "/learning") {
      setValue("learning");
    } else if (location.substr(1) === "/blog") {
      setValue("blog");
    } else if (location.substr(1) === "") {
      setValue("home");
    } else if (location.substr(1) === "contact-us") {
      setValue("other");
    } else if (location.substr(1) === "donate") {
      setValue("other");
    } else if (location.substr(1) === "about") {
      setValue("other");
    }
  }, [active]);

  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };
  const list = (anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === "top" || anchor === "bottom",
      })}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <div className={classes.toolbar}>
        <a className={classes.colorWhite} href="https://mrdiv.dev">
          <h1 className="header-logo">{div}</h1>
        </a>
      </div>
      <Divider />
      <List>
        <Link to={`/donate`}>
          <ListItem
            onClick={() => setActive("#/donate")}
            className={active === "#/donate" ? classes.active : ""}
            button
            key={"حمایت"}
          >
            <ListItemText
              className={classes.textRight + " textBold"}
              primary={"حمایت"}
            />
            <ListItemIcon className={classes.textRight}>
              {<ThumbUp />}
            </ListItemIcon>
          </ListItem>
        </Link>
        <Link to={`/contact-us`}>
          <ListItem
            onClick={() => setActive("#/contact-us")}
            className={active === "#/contact-us" ? classes.active : ""}
            button
            key={"تماس با ما"}
          >
            <ListItemText
              className={classes.textRight + " textBold"}
              primary={"تماس با ما"}
            />
            <ListItemIcon className={classes.textRight}>
              {<ContactMail />}
            </ListItemIcon>
          </ListItem>
        </Link>
        <Link to={`/about`}>
          <ListItem
            onClick={() => setActive("#/about")}
            className={active === "#/about" ? classes.active : ""}
            button
            key={"درباره‌ی ما"}
          >
            <ListItemText
              className={classes.textRight + " textBold"}
              primary={"درباره‌ی ما"}
            />
            <ListItemIcon className={classes.textRight}>
              {<Info />}
            </ListItemIcon>
          </ListItem>
        </Link>
      </List>
    </div>
  );

  return (
    <Fragment>
      <BottomNavigation
        value={value}
        onChange={handleChange}
        className={classes.root + " " + classes.navigationShadow}
      >
        <BottomNavigationAction
          component={Link}
          to={`/`}
          label="خانه"
          value="home"
          icon={<Home />}
          
        />
        <BottomNavigationAction
          component={Link}
          to={`/learning`}
          label="آموزش‌ها"
          value="learning"
          icon={<MenuBook />}
        />
        <BottomNavigationAction
          component={Link}
          to="/blog"
          label="بلاگ"
          value="blog"
          icon={<Book />}
        />
        <BottomNavigationAction
          onClick={toggleDrawer("right", true)}
          label="سایر"
          value="other"
          icon={<MenuOpen />}
        />
        <Drawer
          anchor={"right"}
          open={state["right"]}
          onClose={toggleDrawer("right", false)}
          className={classes.drawer}
          classes={{
            paper: classes.drawerPaper,
          }}
        >
          {list("right")}
        </Drawer>
      </BottomNavigation>
      <main className={classes.content}>
        <Switch>
          <Route path={`/`} exact>
            <HomePage />
          </Route>
          <Route path={`/learning`}>
            <LearningPage />
          </Route>
          <Route path={`/blog`}>
            <Blog />
          </Route>
          <Route path={`/donate`}>
            <Donate />
          </Route>
          <Route path={`/about`}>
            <About />
          </Route>
          <Route path={`/contact-us`}>
            <ContactUs />
          </Route>
          <Route path={`/send-course`}>
            <SendCourse />
          </Route>
        </Switch>
      </main>
    </Fragment>
  );
};

export default App;
