import React, { Fragment, useEffect, useState } from 'react'
import { Container, CircularProgress, makeStyles } from '@material-ui/core'


const useStyle = makeStyles(({
    header:{
        textAlign: "center",
        paddingBottom: "30px"
    }
}))


const ContactUs = () =>{
    const [isloading, setIsloading] = useState(true);
    const classes = useStyle();
    useEffect(() => {
        const timer = setTimeout(() => {
          setIsloading(false)
        }, 4000);
        return () => clearTimeout(timer);
      }, []);
    return(
        <Fragment>
            <Container className={(isloading ? "progressBarCont" : "")}>
                <h1 className={classes.header}>تماس با ما</h1>

                    <iframe className="formaloClass" src="http://formaloo.com/ffRXz?client-id:6c809382c649e67f4c1053f5269dce26ed0ee353"></iframe>

            </Container>
        </Fragment>
    )
}

export default ContactUs