import React, { Fragment } from "react";
import { useParams } from "react-router-dom";
import Helmet from "react-helmet";

const SinglePage = (props) => {
  let { post } = useParams();
  let notFound = false;
  const firstData = props.data.map((data) => {
    if (data.url === "/" + post) {
      notFound = true;
      return (
      <Fragment>
        <Helmet>
            <title>{data.title + " - " + props.pageTitle}</title>
        </Helmet>
          <h1>{data.id}</h1>
      </Fragment>
      );
    }
    return null;
  });
  return (
    <Fragment>
      {notFound ? firstData : <h1>Not Found</h1>}
    </Fragment>
  );
};

export default SinglePage;
