import React, { Fragment } from "react";
import {
  Container,
  CardHeader,
  Avatar,
  IconButton,
  CardMedia,
  CardContent,
  Typography,
  CardActions,
  Card,
  Collapse,
  Link as MaterialLink,
} from "@material-ui/core";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import { Link, useRouteMatch } from "react-router-dom";
import More from "@material-ui/icons/MoreRounded";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import { red } from "@material-ui/core/colors";
import { Avatar as profileImg } from "./../../assets/img/Img";
import Telegram from "@material-ui/icons/Telegram";
import Twitter from "@material-ui/icons/Twitter";
import Instagram from "@material-ui/icons/Instagram";
import WhatsApp from "@material-ui/icons/WhatsApp";

const useStyles = makeStyles((theme) => ({
  root: {
    textAlign: "center",
    borderRadius: "0 !important",
    paddingTop: 16,
    paddingBottom: 16,
    paddingRight: "7px !important",
    paddingLeft: "7px !important",
  },
  media: {
    height: 0,
    paddingTop: "56.25%", // 16:9
  },
  expand: {
    transform: "rotate(0deg)",
    marginLeft: "auto",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: "rotate(180deg)",
  },
  avatar: {
    backgroundColor: red[500],
  },
  linkClass: {
    display: "contents",
    color: "rgba(0, 0, 0, 0.54)",
    fontSize: "14px !important"
  },
  linkClassText: {
    marginLeft: "10px",
  },
  radius: {
    borderRadius: "5px",
  },
  dirRtl: {
    direction: "rtl",
  },
  teleHover: {
    color: "rgba(21,101,192 ,1)",
    transition: ".6s",
  },
  twHover: {
    color: "rgba(33,150,243 ,1)",
    transition: ".6s",
  },
  instaHover: {
    color: "rgba(255,23,68 ,1)",
    transition: ".6s",
  },
  whHover: {
    color: "rgba(0,150,136 ,1)",
    transition: ".6s",
  },
  icons: {
    fontSize: "20px !important",
    transition: ".6s",
  },
  paper: {
    padding: "20px",
  },
  displayFlex: {
    display: "flex",
  },
}));

const GeneralPage = (props) => {
  let match = useRouteMatch();
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  return (
    <Fragment>
      <Container fixed className={classes.root}>
        <Card className={classes.root}>
          <CardHeader
            className={classes.dirRtl}
            avatar={
              <Avatar aria-label="recipe" className={classes.avatar}>
                <img src={profileImg} alt="avatar" />
              </Avatar>
            }
            title={<Link className="titleFontSize" to={match.url + props.url}> {props.title} </Link>}
            subheader={props.date}
          />
          <CardMedia
            className={classes.media}
            image={props.img}
            title={props.title}
          />

          <CardContent>
            <Typography
              className={classes.dirRtl}
              variant="body2"
              color="textSecondary"
              component="p"
            >
              {props.summery}
            </Typography>
          </CardContent>
          <CardActions disableSpacing>
            <IconButton
              className={classes.radius}
              aria-label="add to favorites"
            >
              <Link className={classes.linkClass} to={match.url + props.url}>
                <More />{" "}
                <span className={classes.linkClassText}>ادامه‌ی مطلب</span>
              </Link>
            </IconButton>
            <IconButton aria-label="share">
              <MaterialLink className={classes.displayFlex} href="https://t.me">
                <Telegram className={classes.icons + " " + classes.teleHover} />
              </MaterialLink>
            </IconButton>
            <IconButton aria-label="share">
              <MaterialLink className={classes.displayFlex} href="https://t.me">
                <Twitter className={classes.icons + " " + classes.twHover} />
              </MaterialLink>
            </IconButton>
            <IconButton aria-label="share">
              <MaterialLink className={classes.displayFlex} href="https://t.me">
                <Instagram
                  className={classes.icons + " " + classes.instaHover}
                />
              </MaterialLink>
            </IconButton>
            <IconButton aria-label="share">
              <MaterialLink className={classes.displayFlex} href="https://t.me">
                <WhatsApp className={classes.icons + " " + classes.whHover} />
              </MaterialLink> 
            </IconButton>
            <IconButton
              className={clsx(classes.expand, {
                [classes.expandOpen]: expanded,
              })}
              onClick={handleExpandClick}
              aria-expanded={expanded}
              aria-label="show more"
            >
              <ExpandMoreIcon />
            </IconButton>
          </CardActions>
          <Collapse in={expanded} timeout="auto" unmountOnExit>
            <CardContent className={classes.dirRtl}>
              <Typography paragraph>{props.desc}</Typography>
            </CardContent>
          </Collapse>
        </Card>
      </Container>
    </Fragment>
  );
};

export default GeneralPage;
