import React, { Fragment } from "react";
import GeneralPage from "./other/GeneralPage";
import data from "../data/learning/data";
import { Route, Switch, useRouteMatch } from "react-router-dom";
import SinglePage from "./other/SinglePage";
import {
  Grid,
  makeStyles,
} from "@material-ui/core";
import Helmet from "react-helmet";

const pageTitle = "آموزش‌ها";
const firstData = data.map((data) => {
  return (
    <GeneralPage
      key={data.id}
      title={data.title}
      url={data.url}
      id={data.id}
      img={data.img}
      desc={data.desc}
      summery={data.summery}
      date={data.date}
    />
  );
});

const useStyles = makeStyles((theme) => ({
  root: {
    padding: "0",
  },
  fixedGrid: {
    position: "fixed",
    maxWidth: "28%",
  },
  media: {
    height: 220,
  },
  dirRtl: {
    direction: "rtl",
  },
  route180: {
    transform: "rotate(180deg)",
  },
  mr10: {
    marginRight: "10px",
  },
  paper2:{
    top: "57%"
  }
}));

const LearningPage = () => {
  const classes = useStyles();
  let match = useRouteMatch();
  return (
    <Fragment>
      <Helmet>
        <title>{pageTitle}</title>
      </Helmet>
      <Switch>
        <Route path={match.path + "/:post"}>
          <SinglePage pageTitle={pageTitle} data={data} />
        </Route>
        <Route path={match.path}>
          <div className={classes.root}>
            <Grid justify="center" spacing={0}>
             <Grid item xl={12} lg={12} md={12} sm={12} xs={12}>
                {firstData}
              </Grid>
            </Grid>
          </div>
        </Route>
      </Switch>
    </Fragment>
  );
};

export default LearningPage;
